export const processSalaryFieldAccom = (from, to) => {
    let result = '/ ';

    if (from === 0 && to === 0) {
        return result + `  `;
    } else if (from && to) {
        return result + `${from} - ${to} `;
    } else if (from) {
        return result + ` ${from} `;
    } else if (to) {
        return result + `${to} `;
    }

    return ' ';
};